import {
  Component,
  ViewChild,
  ElementRef,
  Renderer2,
  Input,
  SimpleChanges,
} from "@angular/core";
import { CommonModule } from "@angular/common";

import type { VideoProps } from "./video.types";

@Component({
  selector: "builder-video",
  template: `
    <div [ngStyle]="node_0_div">
      <video
        class="builder-video"
        #videoRef
        [attr.preload]="lazyLoad ? 'none' : preload || 'metadata'"
        [ngStyle]="node_1_video"
        [attr.poster]="posterImage"
        #elRef0
      >
        <source type="video/mp4" #elRef1 />
      </video>
      <ng-container *ngIf="node_3_Show">
        <div [ngStyle]="node_4_div"></div>
      </ng-container>
      <ng-container *ngIf="builderBlock?.children?.length && fitContent">
        <div [ngStyle]="node_5_div"><ng-content></ng-content></div>
      </ng-container>
      <ng-container *ngIf="builderBlock?.children?.length && !fitContent">
        <div [ngStyle]="node_6_div"><ng-content></ng-content></div>
      </ng-container>
    </div>
  `,
  styles: [
    `
      :host {
        display: contents;
      }
    `,
  ],
  standalone: true,
  imports: [CommonModule],
})
export default class BuilderVideo {
  @Input() lazyLoad!: VideoProps["lazyLoad"];
  @Input() attributes!: VideoProps["attributes"];
  @Input() fit!: VideoProps["fit"];
  @Input() position!: VideoProps["position"];
  @Input() aspectRatio!: VideoProps["aspectRatio"];
  @Input() video!: VideoProps["video"];
  @Input() fitContent!: VideoProps["fitContent"];
  @Input() builderBlock!: VideoProps["builderBlock"];
  @Input() autoPlay!: VideoProps["autoPlay"];
  @Input() muted!: VideoProps["muted"];
  @Input() controls!: VideoProps["controls"];
  @Input() loop!: VideoProps["loop"];
  @Input() playsInline!: VideoProps["playsInline"];
  @Input() preload!: VideoProps["preload"];
  @Input() posterImage!: VideoProps["posterImage"];

  @ViewChild("videoRef") videoRef!: ElementRef;

  @ViewChild("elRef0") elRef0!: ElementRef;
  @ViewChild("elRef1") elRef1!: ElementRef;

  _listenerFns = new Map<string, () => void>();

  get videoProps() {
    return {
      ...(this.autoPlay === true
        ? {
            autoPlay: true,
          }
        : {}),
      ...(this.muted === true
        ? {
            muted: true,
          }
        : {}),
      ...(this.controls === true
        ? {
            controls: true,
          }
        : {}),
      ...(this.loop === true
        ? {
            loop: true,
          }
        : {}),
      ...(this.playsInline === true
        ? {
            playsInline: true,
          }
        : {}),
    };
  }
  get spreadProps() {
    return {
      ...this.videoProps,
    };
  }
  lazyVideoObserver = undefined;
  node_0_div = null;
  node_1_video = null;
  node_2_source = null;
  node_3_Show = null;
  node_4_div = null;
  node_5_div = null;
  node_6_div = null;
  setAttributes(el: HTMLElement, value: any, changes?: any) {
    if (!el) {
      return;
    }
    const target = typeof changes === "undefined" ? value : changes;
    Object.keys(target).forEach((key) => {
      if (key.startsWith("on")) {
        if (this._listenerFns.has(key)) {
          this._listenerFns.get(key)!();
        }
        this._listenerFns.set(
          key,
          this.renderer.listen(
            el,
            key.replace("on", "").toLowerCase(),
            target[key]
          )
        );
      } else {
        this.renderer.setAttribute(el, key.toLowerCase(), target[key] ?? "");
      }
    });
  }

  constructor(private renderer: Renderer2) {}

  ngOnInit() {
    this.node_0_div = {
      position: "relative",
    };
    this.node_1_video = {
      width: "100%",
      height: "100%",
      ...this.attributes?.style,
      objectFit: this.fit,
      objectPosition: this.position,
      // Hack to get object fit to work as expected and
      // not have the video overflow
      borderRadius: "1px",
      ...(this.aspectRatio
        ? {
            position: "absolute",
          }
        : null),
    };
    this.node_2_source = {
      ...(this.lazyLoad
        ? {
            "data-src": this.video,
          }
        : {
            src: this.video,
          }),
    };
    this.node_3_Show =
      this.aspectRatio &&
      !(this.fitContent && this.builderBlock?.children?.length);
    this.node_4_div = {
      width: "100%",
      paddingTop: this.aspectRatio! * 100 + "%",
      pointerEvents: "none",
      fontSize: "0px",
    };
    this.node_5_div = {
      display: "flex",
      flexDirection: "column",
      alignItems: "stretch",
    };
    this.node_6_div = {
      pointerEvents: "none",
      display: "flex",
      flexDirection: "column",
      alignItems: "stretch",
      position: "absolute",
      top: "0",
      left: "0",
      width: "100%",
      height: "100%",
    };
  }

  ngAfterViewInit() {
    if (this.lazyLoad) {
      const oberver = new IntersectionObserver(function (entries) {
        entries.forEach(function (entry) {
          if (!entry.isIntersecting) return;
          const videoElement = entry.target as HTMLVideoElement;
          try {
            // Convert HTMLCollection to Array and filter for source elements
            Array.from(videoElement.children)
              .filter(
                (child): child is HTMLSourceElement =>
                  child instanceof HTMLElement && child.tagName === "SOURCE"
              )
              .forEach((source) => {
                const src = source.dataset.src;
                if (src) {
                  source.src = src;
                }
              });
            videoElement.load();
            oberver.unobserve(videoElement);
          } catch (error) {
            console.error("Error loading lazy video:", error);
          }
        });
      });
      if (this.videoRef.nativeElement) {
        oberver.observe(this.videoRef.nativeElement);
      }
      this.lazyVideoObserver = oberver;
    }
    this.setAttributes(this.elRef0?.nativeElement, this.spreadProps);
    this.setAttributes(this.elRef1?.nativeElement, this.node_2_source);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (typeof window !== "undefined") {
      this.node_0_div = {
        position: "relative",
      };
      this.node_1_video = {
        width: "100%",
        height: "100%",
        ...this.attributes?.style,
        objectFit: this.fit,
        objectPosition: this.position,
        // Hack to get object fit to work as expected and
        // not have the video overflow
        borderRadius: "1px",
        ...(this.aspectRatio
          ? {
              position: "absolute",
            }
          : null),
      };
      this.node_2_source = {
        ...(this.lazyLoad
          ? {
              "data-src": this.video,
            }
          : {
              src: this.video,
            }),
      };
      this.node_3_Show =
        this.aspectRatio &&
        !(this.fitContent && this.builderBlock?.children?.length);
      this.node_4_div = {
        width: "100%",
        paddingTop: this.aspectRatio! * 100 + "%",
        pointerEvents: "none",
        fontSize: "0px",
      };
      this.node_5_div = {
        display: "flex",
        flexDirection: "column",
        alignItems: "stretch",
      };
      this.node_6_div = {
        pointerEvents: "none",
        display: "flex",
        flexDirection: "column",
        alignItems: "stretch",
        position: "absolute",
        top: "0",
        left: "0",
        width: "100%",
        height: "100%",
      };
      this.setAttributes(
        this.elRef0?.nativeElement,
        this.spreadProps,
        changes["spreadProps"]?.currentValue
      );
      this.setAttributes(
        this.elRef1?.nativeElement,
        this.node_2_source,
        changes["node_2_source"]?.currentValue
      );
    }
  }

  ngOnDestroy() {
    if (this.lazyVideoObserver) {
      this.lazyVideoObserver.disconnect();
    }
    for (const fn of this._listenerFns.values()) {
      fn();
    }
  }
}
