import {
  Component,
  ViewChild,
  ElementRef,
  Renderer2,
  Input,
  SimpleChanges,
} from "@angular/core";
import { CommonModule } from "@angular/common";

import Blocks from "../../components/blocks/blocks";
import InlinedScript from "../../components/inlined-script";
import InlinedStyles from "../../components/inlined-styles";
import { isEditing } from "../../functions/is-editing";
import { isPreviewing } from "../../functions/is-previewing";
import { getDefaultCanTrack } from "../../helpers/canTrack";
import { userAttributesService } from "../../helpers/user-attributes";
import {
  checkShouldRenderVariants,
  filterWithCustomTargeting,
  getBlocksToRender,
  getPersonalizationScript,
} from "./helpers";
import type { PersonalizationContainerProps } from "./personalization-container.types";

@Component({
  selector: "personalization-container",
  template: `
    <div #rootRef [class]="node_0_div" #elRef0>
      <ng-container *ngIf="shouldRenderVariants">
        <ng-container
          *ngFor="let variant of variants; index as index; trackBy: trackByVariant0"
        >
          <template [attr.data-variant-id]="node_1_template(variant, index)">
            <blocks
              [blocks]="variant.blocks"
              [parent]="builderBlock?.id"
              [path]="node_2_Blocks(variant, index)"
            ></blocks>
          </template>
        </ng-container>
        <inlined-styles
          [nonce]="builderContext?.nonce || ''"
          [styles]="hideVariantsStyleString"
          [id]="node_3_InlinedStyles"
        ></inlined-styles>
        <inlined-script
          [nonce]="builderContext?.nonce || ''"
          [scriptStr]="scriptStr"
          [id]="node_4_InlinedScript"
        ></inlined-script>
      </ng-container>
      <blocks
        [blocks]="blocksToRender.blocks"
        [parent]="builderBlock?.id"
        [path]="blocksToRender.path"
      ></blocks>
    </div>
  `,
  styles: [
    `
      :host {
        display: contents;
      }
    `,
  ],
  standalone: true,
  imports: [CommonModule, Blocks, InlinedStyles, InlinedScript],
})
export default class PersonalizationContainer {
  @Input() builderContext!: PersonalizationContainerProps["builderContext"];
  @Input() attributes!: PersonalizationContainerProps["attributes"];
  @Input() builderBlock!: PersonalizationContainerProps["builderBlock"];
  @Input() variants!: PersonalizationContainerProps["variants"];
  @Input() previewingIndex!: PersonalizationContainerProps["previewingIndex"];

  @ViewChild("rootRef") rootRef!: ElementRef;

  @ViewChild("elRef0") elRef0!: ElementRef;

  _listenerFns = new Map<string, () => void>();

  userAttributes = userAttributesService.getUserAttributes();
  scriptStr = null;
  unsubscribers = [];
  shouldRenderVariants = null;
  isHydrated = false;
  get filteredVariants() {
    return (this.variants || []).filter((variant) => {
      return filterWithCustomTargeting(
        {
          ...(this.builderContext?.rootState?.locale
            ? {
                locale: this.builderContext?.rootState?.locale,
              }
            : {}),
          ...(this.userAttributes as any),
        },
        variant.query,
        variant.startDate,
        variant.endDate
      );
    });
  }
  get blocksToRender() {
    return getBlocksToRender({
      variants: this.variants,
      fallbackBlocks: this.builderBlock?.children,
      isHydrated: this.isHydrated,
      filteredVariants: this.filteredVariants,
      previewingIndex: this.previewingIndex,
    });
  }
  get hideVariantsStyleString() {
    return (this.variants || [])
      .map(
        (_, index) =>
          `[data-variant-id="${this.builderBlock?.id}-${index}"] { display: none; } `
      )
      .join("");
  }
  node_0_div = null;
  node_1_template = (variant, index) => `${this.builderBlock?.id}-${index}`;
  node_2_Blocks = (variant, index) =>
    `component.options.variants.${index}.blocks`;
  node_3_InlinedStyles = null;
  node_4_InlinedScript = null;
  setAttributes(el: HTMLElement, value: any, changes?: any) {
    if (!el) {
      return;
    }
    const target = typeof changes === "undefined" ? value : changes;
    Object.keys(target).forEach((key) => {
      if (key.startsWith("on")) {
        if (this._listenerFns.has(key)) {
          this._listenerFns.get(key)!();
        }
        this._listenerFns.set(
          key,
          this.renderer.listen(
            el,
            key.replace("on", "").toLowerCase(),
            target[key]
          )
        );
      } else {
        this.renderer.setAttribute(el, key.toLowerCase(), target[key] ?? "");
      }
    });
  }
  trackByVariant0(index, variant) {
    return index;
  }

  constructor(private renderer: Renderer2) {}

  ngOnInit() {
    this.scriptStr = getPersonalizationScript(
      this.variants,
      this.builderBlock?.id || "none",
      this.builderContext?.rootState?.locale as string | undefined
    );

    this.shouldRenderVariants = checkShouldRenderVariants(
      this.variants,
      getDefaultCanTrack(this.builderContext?.canTrack)
    );
    this.node_0_div = `builder-personalization-container ${
      this.attributes?.className || ""
    }`;
    this.node_3_InlinedStyles = `variants-styles-${this.builderBlock?.id}`;
    this.node_4_InlinedScript = `variants-script-${this.builderBlock?.id}`;

    if (typeof window !== "undefined") {
      this.isHydrated = true;
      const unsub = userAttributesService.subscribeOnUserAttributesChange(
        (attrs) => {
          this.userAttributes = attrs;
        }
      );
      if (!(isEditing() || isPreviewing())) {
        const variant = this.filteredVariants[0];
        if (this.rootRef.nativeElement) {
          this.rootRef.nativeElement.dispatchEvent(
            new CustomEvent("builder.variantLoaded", {
              detail: {
                variant: variant || "default",
                content: this.builderContext?.content,
              },
              bubbles: true,
            })
          );
          const observer = new IntersectionObserver((entries) => {
            entries.forEach((entry) => {
              if (entry.isIntersecting && this.rootRef.nativeElement) {
                this.rootRef.nativeElement.dispatchEvent(
                  new CustomEvent("builder.variantDisplayed", {
                    detail: {
                      variant: variant || "default",
                      content: this.builderContext?.content,
                    },
                    bubbles: true,
                  })
                );
              }
            });
          });
          observer.observe(this.rootRef.nativeElement);
        }
      }
      this.unsubscribers.push(unsub);
    }
  }

  ngAfterViewInit() {
    this.setAttributes(this.elRef0?.nativeElement, this.attributes);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (typeof window !== "undefined") {
      this.node_0_div = `builder-personalization-container ${
        this.attributes?.className || ""
      }`;
      this.node_3_InlinedStyles = `variants-styles-${this.builderBlock?.id}`;
      this.node_4_InlinedScript = `variants-script-${this.builderBlock?.id}`;
      this.setAttributes(
        this.elRef0?.nativeElement,
        this.attributes,
        changes["attributes"]?.currentValue
      );
    }
  }

  ngOnDestroy() {
    this.unsubscribers.forEach((unsub) => unsub());
    for (const fn of this._listenerFns.values()) {
      fn();
    }
  }
}
