import { Component, Input, SimpleChanges } from "@angular/core";
import { CommonModule } from "@angular/common";

export type BlockProps = {
  block: BuilderBlock;
  context: BuilderContextInterface;
  registeredComponents: RegisteredComponents;
  linkComponent: any;
};

import { TARGET } from "../../constants/target";
import type {
  BuilderContextInterface,
  RegisteredComponents,
} from "../../context/types";
import { getBlockComponentOptions } from "../../functions/get-block-component-options";
import { getProcessedBlock } from "../../functions/get-processed-block";
import { isPreviewing } from "../../server-index";
import type { BuilderBlock } from "../../types/builder-block";
import DynamicDiv from "../dynamic-div";
import { bindAnimations } from "./animator";
import {
  getComponent,
  getInheritedStyles,
  getRepeatItemData,
  provideBuilderBlock,
  provideBuilderContext,
  provideLinkComponent,
  provideRegisteredComponents,
} from "./block.helpers";
import BlockStyles from "./components/block-styles";
import BlockWrapper from "./components/block-wrapper";
import type { ComponentProps } from "./components/component-ref/component-ref.helpers";
import ComponentRef from "./components/component-ref/component-ref";
import RepeatedBlock from "./components/repeated-block";

@Component({
  selector: "block",
  template: `
    <ng-container *ngIf="canShowBlock">
      <block-styles [block]="processedBlock" [context]="context"></block-styles>
      <ng-container *ngIf="!blockComponent?.noWrap">
        <ng-container *ngIf="!repeatItem">
          <block-wrapper
            [Wrapper]="Tag"
            [block]="processedBlock"
            [context]="context"
          >
            <component-ref
              [componentRef]="componentRefProps.componentRef"
              [componentOptions]="componentRefProps.componentOptions"
              [blockChildren]="componentRefProps.blockChildren"
              [context]="componentRefProps.context"
              [registeredComponents]="componentRefProps.registeredComponents"
              [linkComponent]="componentRefProps.linkComponent"
              [builderBlock]="componentRefProps.builderBlock"
              [includeBlockProps]="componentRefProps.includeBlockProps"
              [isInteractive]="componentRefProps.isInteractive"
            ></component-ref>
            <ng-container
              *ngFor="let child of childrenWithoutParentComponent; trackBy: trackByChild0"
            >
              <block
                [block]="child"
                [registeredComponents]="registeredComponents"
                [linkComponent]="linkComponent"
                [context]="context"
              ></block>
            </ng-container>
          </block-wrapper>
        </ng-container>
        <ng-container *ngIf="!(!repeatItem)">
          <ng-container
            *ngFor="let data of repeatItem; index as index; trackBy: trackByData1"
          >
            <repeated-block
              [repeatContext]="data.context"
              [block]="data.block"
              [registeredComponents]="registeredComponents"
              [linkComponent]="linkComponent"
            ></repeated-block>
          </ng-container>
        </ng-container>
      </ng-container>
      <ng-container *ngIf="!(!blockComponent?.noWrap)">
        <ng-container *ngIf="!repeatItem">
          <component-ref
            [componentRef]="componentRefProps.componentRef"
            [componentOptions]="componentRefProps.componentOptions"
            [blockChildren]="componentRefProps.blockChildren"
            [context]="componentRefProps.context"
            [registeredComponents]="componentRefProps.registeredComponents"
            [linkComponent]="componentRefProps.linkComponent"
            [builderBlock]="componentRefProps.builderBlock"
            [includeBlockProps]="componentRefProps.includeBlockProps"
            [isInteractive]="componentRefProps.isInteractive"
          ></component-ref>
        </ng-container>
        <ng-container *ngIf="!(!repeatItem)">
          <ng-container
            *ngFor="let data of repeatItem; index as index; trackBy: trackByData2"
          >
            <repeated-block
              [repeatContext]="data.context"
              [block]="data.block"
              [registeredComponents]="registeredComponents"
              [linkComponent]="linkComponent"
            ></repeated-block>
          </ng-container>
        </ng-container>
      </ng-container>
    </ng-container>
  `,
  styles: [
    `
      :host {
        display: contents;
      }
    `,
  ],
  standalone: true,
  imports: [
    CommonModule,
    BlockStyles,
    RepeatedBlock,
    ComponentRef,
    BlockWrapper,
    Block,
  ],
})
export default class Block {
  @Input() block!: BlockProps["block"];
  @Input() context!: BlockProps["context"];
  @Input() registeredComponents!: BlockProps["registeredComponents"];
  @Input() linkComponent!: BlockProps["linkComponent"];

  get repeatItem() {
    return getRepeatItemData({
      block: this.block,
      context: this.context,
    });
  }
  _processedBlock = {
    value: null as BuilderBlock | null,
    update: false,
  };
  get processedBlock() {
    const blockToUse = this.block.repeat?.collection
      ? this.block
      : getProcessedBlock({
          block: this.block,
          localState: this.context.localState,
          rootState: this.context.rootState,
          rootSetState: this.context.rootSetState,
          context: this.context.context,
        });
    return blockToUse;
  }
  get blockComponent() {
    return getComponent({
      block: this.processedBlock,
      registeredComponents: this.registeredComponents,
      model: this.context.model,
    });
  }
  get Tag() {
    const shouldUseLink =
      this.block.tagName === "a" ||
      this.processedBlock.properties?.href ||
      this.processedBlock.href;
    if (shouldUseLink) {
      return this.linkComponent || "a";
    }
    return DynamicDiv;
  }
  get canShowBlock() {
    if (this.block.repeat?.collection) {
      if (this.repeatItem?.length) return true;
      return false;
    }
    const shouldHide =
      "hide" in this.processedBlock ? this.processedBlock.hide : false;
    const shouldShow =
      "show" in this.processedBlock ? this.processedBlock.show : true;
    return shouldShow && !shouldHide;
  }
  get childrenWithoutParentComponent() {
    /**
     * When there is no `componentRef`, there might still be children that need to be rendered. In this case,
     * we render them outside of `componentRef`.
     * NOTE: We make sure not to render this if `repeatItemData` is non-null, because that means we are rendering an array of
     * blocks, and the children will be repeated within those blocks.
     */
    const shouldRenderChildrenOutsideRef =
      !this.blockComponent?.component && !this.repeatItem;
    return shouldRenderChildrenOutsideRef
      ? this.processedBlock.children ?? []
      : [];
  }
  get componentRefProps() {
    return {
      blockChildren: this.processedBlock.children ?? [],
      componentRef: this.blockComponent?.component,
      componentOptions: {
        ...getBlockComponentOptions(this.processedBlock, this.context),
        ...provideBuilderBlock(this.blockComponent, this.processedBlock),
        ...provideBuilderContext(this.blockComponent, this.context),
        ...provideLinkComponent(this.blockComponent, this.linkComponent),
        ...provideRegisteredComponents(
          this.blockComponent,
          this.registeredComponents,
          this.context.model
        ),
      },
      context: this.context,
      linkComponent: this.linkComponent,
      registeredComponents: this.registeredComponents,
      builderBlock: this.processedBlock,
      includeBlockProps: this.blockComponent?.noWrap === true,
      isInteractive: !(this.blockComponent?.isRSC && TARGET === "rsc"),
    };
  }
  trackByChild0(_, child) {
    return child.id;
  }
  trackByData1(index, data) {
    return index;
  }
  trackByData2(index, data) {
    return index;
  }

  ngOnInit() {
    if (typeof window !== "undefined") {
      const blockId = this.processedBlock.id;
      const animations = this.processedBlock.animations;
      if (animations && blockId) {
        // in Angular, the DynamicDiv sets attributes (builder-id) on the element late (ngAfterViewInit)
        // so we need to wait for them to be attached to the DOM before calling this else we'll not find the element
        requestAnimationFrame(() => {
          bindAnimations(
            animations.map((animation) => ({
              ...animation,
              elementId: blockId,
            }))
          );
        });
      }
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (typeof window !== "undefined") {
    }
  }
}
